import { Component, OnInit } from '@angular/core';
import { PurchasingPurchaseRequestService, PurchasingPurchaseRequestItemService } from 'src/app/sdk/purchasing/services';
import { DirektoriPegawaiMasterService, CompanyKantorService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { AssetModelxService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Purchase_request, Purchase_request_item } from 'src/app/sdk/purchasing/models';
import { Pegawai_master, Kantor, Divisi } from 'src/app/sdk/hr/models';
import { Modelx } from 'src/app/sdk/asset/models';
import { FormControl } from '@angular/forms';
import { SearchItemComponent } from 'src/app/components/search-item/search-item/search-item.component';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private purchasingPurchaseRequestService: PurchasingPurchaseRequestService,
        private purchasingPurchaseRequestItemService: PurchasingPurchaseRequestItemService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private drawerService: NzDrawerService
    ) { }

    dataRequest: Purchase_request = {}
    list_item: Purchase_request_item[] = []
    id_purchase_request: string = ''

    listPegawai: Pegawai_master[] = []
    listKantor: Kantor[] = []
    listDivisi: Divisi[] = []
    listModel: Modelx[] = []

    ngOnInit() {
        this.id_purchase_request = this.activatedRoute.snapshot.paramMap.get('id_purchase_request')
        this.getData()
        this.form()
        this.getPreeData()
        this.getListItem()

    }
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })

        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })

        this.companyDivisiService.getCompanyDivisis().subscribe(data => {
            this.listDivisi = data.data
        })
    }


    getData() {
        this.purchasingPurchaseRequestService.getPurchasingPurchaseRequestsId(this.id_purchase_request).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }


    dataForm: FormGroupTypeSafe<Purchase_request>
    form() {
        this.dataForm = this.fb.group<Purchase_request>({
            id_pegawai_request: new FormControl(''),
            id_kantor_request: new FormControl(''),
            id_purchase_request: new FormControl(''),
            id_divisi: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
            purpose_request: new FormControl(''),
            tanggal_request: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            nomor_purchase_request: new FormControl(''),
            updated_at: new FormControl(''),
            validasi: new FormControl('')
        })
    }

    // ## LIST ITEM -----------------------------------
    id_model: string = null;
    add_item() {
        const drawerRef = this.drawerService.create<SearchItemComponent, { value: string }, string>({
            nzTitle: 'Search Model',
            nzContent: SearchItemComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_model = data;
                this.beforeModel()
            }
        });
    }


    getListItem() {
        this.purchasingPurchaseRequestItemService.getPurchasingPurchaseRequestItems(JSON.stringify({ id_purchase_request: this.id_purchase_request })).subscribe(
            data => {
                this.list_item = data.data
            }
        )
    }

    deleteItem(id) {
        this.spinner.show()
        this.purchasingPurchaseRequestItemService.deletePurchasingPurchaseRequestItemsId(id).subscribe(
            data => {
                this.getListItem()
                this.spinner.hide()
            }
        )
    }


    dataItem: any = {};

    beforeModel() {
        console.log("panggie ko")
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {
                this.dataItem.keyword = data.data.keyword;
                this.dataItem.part_number = data.data.part_number;
                this.dataItem.id_model = data.data.id_model;

                console.log(this.dataItem)
            }
        )
    }

    urgency: string = ''
    purpose: string = ''
    qty: number = 0
    simpan_item() {
        if (this.dataItem.qty < 1) {
            alert('tidak boleh kosong');
            return false;
        }

        let param: Purchase_request_item = {
            id_model: this.dataItem.id_model,
            qty: this.dataItem.qty,
            id_purchase_request: this.id_purchase_request,
            urgency: this.urgency,
            purpose: this.purpose
        };

        this.spinner.show()
        //simpan ka server
        this.purchasingPurchaseRequestItemService.postPurchasingPurchaseRequestItems(param).subscribe(
            data => {
                this.id_model = null;
                this.spinner.hide();
                this.getListItem()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

    // ## END  LIST ITEM -----------------------------------



    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        if (this.list_item.length < 1) {
            this.pesan.pesanWarningForm('Anda harus pilih Item Pembelian!')
            this.spinner.hide()
            return
        }

        this.dataRequest = this.dataForm.value
        this.purchasingPurchaseRequestService.putPurchasingPurchaseRequestsId({ id: this.id_purchase_request, body: this.dataRequest }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/purchasing/pr/request/list'])
    }


}

