import { Divisi } from 'src/app/sdk/hr/models/divisi';
import { CompanyDivisiService, DirektoriPegawaiMasterService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { PurchasingPurchaseRequestService, PurchasingPurchaseRequestItemService } from 'src/app/sdk/purchasing/services';
import { AssetModelxService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Purchase_request, Purchase_request_item } from 'src/app/sdk/purchasing/models';
import { Pegawai_master, Kantor } from 'src/app/sdk/hr/models';
import { ToolsDataFlowService, ToolsDataFlowDetailService, ToolsLogFlowService } from 'src/app/sdk/user/services';
import { Data_flow_detail } from 'src/app/sdk/user/models';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private purchasingPurchaseRequestService: PurchasingPurchaseRequestService,
        private purchasingPurchaseRequestItemService: PurchasingPurchaseRequestItemService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private toolsDataFlowService: ToolsDataFlowService,
        private toolsDataFlowDetailService: ToolsDataFlowDetailService,
        private toolsLogFlowService: ToolsLogFlowService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private standarApprovalService: StandarApprovalService,
    ) { }

    dataRequest: Purchase_request = {}
    list_item: Purchase_request_item[] = []
    id_purchase_request: string = ''

    dataPegawai: Pegawai_master = {}
    dataKantor: Kantor = {}
    dataDivisi: Divisi = {}
    dataFlowdetail: Data_flow_detail = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            params => {
                console.log(params.get('id_purchase_request'))
                if (params.get('id_purchase_request')) {
                    this.id_purchase_request = params.get('id_purchase_request')

                    this.getDetailRequest()
                    this.getDetailItem()
                    this.getLog()

                }

            }
        )



        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailRequest()
                    this.getDetailItem()
                }
            }
        )


    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getDetailRequest() {
        this.spinner.show()
        this.purchasingPurchaseRequestService.getPurchasingPurchaseRequestsId(this.id_purchase_request).subscribe(
            data => {
                this.dataRequest = data.data;
                this.getPegawai(data.data.id_pegawai_request);
                this.getkantor(data.data.id_kantor_request);
                this.getDivisi(data.data.id_divisi);
                this.getFlowDetails(data.data.id_data_flow_detail)
                this.id_data_flow = data.data.id_data_flow;
                this.spinner.hide()

                this.params = {
                    id: data.data.id_purchase_request,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_purchase_request"
                }
                this.standarApprovalService.param.next(this.params)
            }
        )
    }

    getPegawai(id_pegawai_request) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai_request).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getkantor(id_kantor_request) {
        this.companyKantorService.getCompanyKantorsId(id_kantor_request).subscribe(
            data => {
                this.dataKantor = data.data;
            }
        )
    }

    getFlowDetails(id_data_flow_detail) {
        this.spinner.show()
        this.toolsDataFlowDetailService.getToolsDataFlowDetailsId(id_data_flow_detail).subscribe(
            data => {
                this.dataFlowdetail = data.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_purchase_request })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    getDivisi(id_divisi) {
        this.companyDivisiService.getCompanyDivisisId(id_divisi).subscribe(
            data => {
                this.dataDivisi = data.data;
            }
        )
    }

    getDetailItem() {
        this.purchasingPurchaseRequestItemService.getPurchasingPurchaseRequestItems(JSON.stringify({ id_purchase_request: this.id_purchase_request })).subscribe(
            res => {
                this.list_item = res.data;
                this.spinner.hide()
            }
        )
    }

    flowDetail: Data_flow_detail[] = []

    getDataFlowDetail() {
        this.toolsDataFlowDetailService.getToolsDataFlowDetails(JSON.stringify({ id_data_flow: this.id_data_flow })).subscribe(
            data => {
                this.flowDetail = data.data
            }
        )
    }

    goToBack() {
        this.router.navigate(['/purchasing/pr/request/list'])
    }



}
